#emoji{
	margin:0;
	padding:0;
    position: absolute;
    left: 373px;
    /*height: 300px;*/
    width: 300px;
    box-sizing:border-box;
}
.emoji-ul{
    margin:18px;
    padding: 0;
}
#emoji-container{
    padding: 0;
    margin: 0;
	height: 100%;
	width: 100%;
	position: relative;
}
#emoji-content{
	height: 100%;
	width: 100%;
    background-color: gray;
}

#emoji-triangle{
	position: absolute;
    height: 10px;
    width: 10px;
    top:-10px;
    background-color: gray;
}
.triangle-left{
    -webkit-transform-origin:right bottom;
        -ms-transform-origin:right bottom;
            transform-origin:right bottom;
    -webkit-transform:rotate(-45deg);
        -ms-transform:rotate(-45deg);
            transform:rotate(-45deg);
    left: auto;
    right:10px;
}

.triangle-right{
    right: auto;
    left: 10px;
    -webkit-transform-origin: left bottom;
        -ms-transform-origin: left bottom;
            transform-origin: left bottom;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg)
}


.emoji-clearfix{
    display: inline-block;
}
.emoji-clearfix:after{
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}
.emoji-ul li{
    list-style: none;
    float: left;
    margin: 5px;
    height: 34px;
    width:34px;
}
/*layer*/
#emoji-layer{
    left: 0;
    right: 0;
    top:0;
    bottom: 0;
    position: absolute;
}
